import AppBody from '../AppBody'
import { Wrapper } from '../Pool/styleds'
import Axios from 'axios'
import { baseUrlV1 } from '../../moonbase_address.json'
import React ,{useEffect,useState} from 'react'
import { useActiveWeb3React } from '../../hooks'


const Guohui = () => {
    const { account } = useActiveWeb3React()
    const [buyback,setBuyback]=useState('--')
    const [cumulative ,setCumulative ]=useState('--')
    const [giveout,setGiveout]=useState('--')
    const getData=async()=>{
        const url = `${baseUrlV1}/dex/congress-data`
        let header = {
            headers: {
                "Show-Language": 'zh-CN',
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'guohui');
        let data=r.data.data
        setBuyback(data.buy_back)
        setCumulative(data.cumulative)
        setGiveout(data.give_out)
    }
    
    useEffect(()=>{
        getData()
    },[])
    

    return (
        <div style={{ width: '90%'}}>
            <div style={{fontSize:'1.2rem',marginBottom:'1.4rem'}}>国会</div>
            <div style={{ padding: '1rem', backgroundColor: '#2C2D32', borderRadius: '0.4rem' }}>
                <div style={{ fontSize: '1rem', marginBottom: '0.4rem', borderBottom: '1px #000 solid', paddingBottom: '0.8rem' }}>国库</div>
                <div style={{ borderBottom: '1px #000 solid', paddingBottom: '0.8rem' }}>
                    <div style={{ color: 'rgba(255,255,255,0.6)', margin: '0.8rem 0' }}>国库累计金额</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <div style={{ fontSize: '1rem' }}>{cumulative}</div>
                        <div style={{ color: 'rgba(255,255,255,0.6)' }}>USDT</div>
                    </div>
                </div>
                <div style={{ borderBottom: '1px #000 solid', paddingBottom: '0.8rem' }}>
                    <div style={{ color: 'rgba(255,255,255,0.6)', margin: '0.8rem 0' }}>国库已分配金额</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <div style={{ fontSize: '1rem' }}>{giveout}</div>
                        <div style={{ color: 'rgba(255,255,255,0.6)' }}>USDT</div>
                    </div>
                </div>
                <div style={{ paddingBottom: '0.8rem' }}>
                    <div style={{ color: 'rgba(255,255,255,0.6)', margin: '0.8rem 0' }}>国库已回购金额</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <div style={{ fontSize: '1rem' }}>{buyback}</div>
                        <div style={{ color: 'rgba(255,255,255,0.6)' }}>USDT</div>
                    </div>
                </div>
            </div>
            <div style={{ padding: '1rem', backgroundColor: '#2C2D32', borderRadius: '0.4rem' ,marginTop:'2rem'}}>
                <div style={{ fontSize: '1rem', marginBottom: '0.4rem', borderBottom: '1px #000 solid', paddingBottom: '0.8rem' }}>DAO</div>
                <div style={{display: 'flex',justifyContent:'center',alignItems:'center',backgroundColor:'#6C6C6C',margin:'1rem 0',padding:'1.4rem 0'}}>尚未开放，敬请期待</div>

            </div>

        </div>
    )
}

export default Guohui