import React, { useEffect, useState } from 'react'
import AppBody from '../AppBody'
import { Wrapper } from '../Pool/styleds'
import Xinfeng from '../../assets/rfs/xinfeng.png'
import CopyIcon from '../../assets/rfs/copy.png'
import styled from 'styled-components'
import { useActiveWeb3React } from '../../hooks'
import Copy from 'copy-to-clipboard'
import { Snackbar } from '@material-ui/core'
import { baseUrlV1 } from '../../moonbase_address.json'
import Axios from 'axios'



const Share = () => {
    const { account } = useActiveWeb3React()
    const [isShow, setIsShow] = useState(false)
    const [userList, setUserList] = useState([])
    const [maxCp,setMaxCp]=useState('--')
    const [otherCp,setOtherCp]=useState('--')
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setIsShow(false);
    };
    const copyText = () => {
        Copy('https://dex.xphoenix.club?id=' + account)
        setIsShow(true)

    }
    const getCp=async()=>{
        const url = `${baseUrlV1}/reward/zone-cp`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'cp')
        setMaxCp(r.data.data.max_zone_cp)
        setOtherCp(r.data.data.other_zone_cp)
    }
    const getUserList = async () => {
        const url = `${baseUrlV1}/user-center/children-list`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'list')
        let data=r.data.data
        let list=data.map(p=>`${p.substring(0, 6)}***${p.substring(p.length - 4, p.length)}`)
        setUserList(list)
    }
    useEffect(() => {
        getUserList()
        getCp()

    }, [account])
    return (
        <AppBody>
            <Wrapper>
                <div style={{ display: 'flex', justifyContent: 'center', position: 'relative' }}>
                    <img style={{ width: '312px', height: '220px',maxWidth:'100%',maxHeight:'100%' }} src={Xinfeng}></img>
                    <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', margin: '1rem', padding: "0 1rem", position: 'absolute' }}>
                        <div style={{ fontSize: '1rem', color: 'rgba(255,255,255,0.6)', marginBottom: '1rem' }}>我的邀请链接</div>
                        <div onClick={copyText} style={{ display: 'flex', alignItems: 'center' }}>
                            <div style={{ textOverflow: 'ellipsis' }}>dex.xphoenix.club...</div>
                            <img style={{ width: '1rem', height: '1rem', marginLeft: '1rem' }} src={CopyIcon}></img>
                        </div>
                    </div>
                </div>
                <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '1.6rem', marginBottom: '1rem' }}>
                    <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem', width: '8rem', margin: '0 1.2rem', borderRadius: '0.4rem' }}>
                        <div style={{ color: 'rgba(255,255,255,0.6)', marginBottom: '0.4rem' }}>最大算力区</div>
                        <div>{maxCp}</div>
                    </div>
                    <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem', width: '8rem', margin: '0 1.2rem', borderRadius: '0.4rem' }}>
                        <div style={{ color: 'rgba(255,255,255,0.6)', marginBottom: '0.4rem' }}>其他算力区</div>
                        <div>{otherCp}</div>
                    </div>
                </div>
                <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem', margin: '0 1.2rem', borderRadius: '0.4rem' }}>
                    <div style={{ color: 'rgba(255,255,255,0.6)', fontSize: '1rem', marginBottom: '0.4rem' }}>我邀请的好友</div>
                    {
                        userList.map((p,index)=>
                            <div key={index} style={{color:'rgba(255,255,255,0.6)',margin:'1rem 0'}}>{index}<span style={{color:'#fff',marginLeft:'1rem'}}>{p}</span></div>
                            )
                    }

                </div>
                <Snackbar
                    open={isShow}
                    autoHideDuration={3000}
                    message='复制成功'
                    onClose={handleClose}
                >
                </Snackbar>
            </Wrapper>
        </AppBody>
    )
}

export default Share