import React, { useEffect, useState } from 'react'
import { ButtonLight } from '../../components/Button'
import { useActiveWeb3React } from '../../hooks'
import AppBody from '../AppBody'
import { Wrapper } from '../Pool/styleds'
import Axios from 'axios'
import { baseUrl, baseUrlV1 } from '../../moonbase_address.json'
import Hehuoren from '../../assets/rfs/hehuoren.png'
import HehuorenText from '../../assets/rfs/hehuoren_text.png'
import { Snackbar } from '@material-ui/core'


const Reward = () => {
    const { account } = useActiveWeb3React()
    const [isShow, setIsShow] = useState(false)
    const [msg, setMsg] = useState('')
    const [allReward, setAllReward] = useState('--')
    const [myReward, setMyReward] = useState('--')
    const [myAllReward, setMyAllReward] = useState('--')
    const [isPartner, setIsPartner] = useState(false)
    const [allRewardPartner, setAllRewardPartner] = useState('--')
    const [myAllRewardPartner, setMyAllRewardPartner] = useState('--')
    const getAllReward = async () => {
        const url = `${baseUrlV1}/dex/lp-data`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        setAllReward(r.data.data)
    }
    const getAllRewardPartner = async () => {
        const url = `${baseUrlV1}/dex/partner-data`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        setAllRewardPartner(r.data.data)
    }
    const getUserReward = async () => {
        const url = `${baseUrlV1}/reward/user-rewards`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        let list = r.data.data
        let rw = 0
        for (let i = 0; i < list.length; i++) {
            if (list[i].coin_en == 'FFT') {
                rw = list[i].usable
                break
            }
        }
        setMyReward(rw)
    }
    const getPartnerReward = async () => {
        const url = `${baseUrlV1}/reward/partner-rewards`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'r')
        let list = r.data.data
        let rw = 0
        for (let i = 0; i < list.length; i++) {
            if (list[i].coin_en == 'FFT') {
                rw = list[i].usable
                break
            }
        }
        setMyAllRewardPartner(rw)
    }
    const getUserAllReward = async () => {
        const url = `${baseUrlV1}/reward/markert-rewards`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        let list = r.data.data
        let rw = 0
        for (let i = 0; i < list.length; i++) {
            if (list[i].coin_en == 'FFT') {
                rw = list[i].usable
                break
            }
        }
        console.log(r, 'my all')
        setMyAllReward(rw)

    }
    const getLevel = async () => {
        const url = `${baseUrlV1}/user-center/member-info`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'level')
        setIsPartner(r.data.data.is_partner)

    }
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setIsShow(false);
    };
    const claimReward = async () => {
        const url = `${baseUrlV1}/order/reward-out`
        let header = {
            headers: {
                Authorization: account
            }
        }
        let data = {
            coin_id: 5
        }
        const r = await Axios.post(url, data, header)
        console.log(r, 'claimReward')
        setMsg(r.data.msg)
        setIsShow(true)
        getUserReward()

    }


    useEffect(() => {
        getAllReward()
        getUserReward()
        getLevel()
        getUserAllReward()
        //partner
        getAllRewardPartner()
        getPartnerReward()

    }, [account]);

    return (
        <AppBody>
            <Wrapper>
                <div style={{ color: '#fff', fontSize: '1.2rem', margin: '1rem 0' }}>权益池</div>
                <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem' }}>
                    <div style={{ color: 'rgba(225,225,225,0.6)' }}>权益池已分配总额</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                        <div style={{ color: '#fff', fontSize: '1.2rem' }}>{allReward}</div>
                        <div style={{ color: 'rgba(255,255,255,0.6)' }}>FFT</div>
                    </div>
                </div>
                <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem', marginTop: '1rem' }}>
                    <div style={{ color: 'rgba(225,225,225,0.6)' }}>待领取数额</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                        <div style={{ color: '#fff', fontSize: '1.2rem' }}>{myReward}</div>
                        <div style={{ color: 'rgba(255,255,255,0.6)' }}>FFT</div>
                    </div>
                    <ButtonLight onClick={claimReward} style={{ height: '1rem', margin: '1rem 0' }}>取出</ButtonLight>
                    <div style={{ color: 'rgba(225,225,225,0.6)' }}>我已获取的数额</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                        <div style={{ color: '#fff', fontSize: '1.2rem' }}>{myAllReward}</div>
                        <div style={{ color: 'rgba(255,255,255,0.6)' }}>FFT</div>
                    </div>
                </div>
                {/* 合伙人 */}
                {
                    isPartner &&
                    <div>
                        <div style={{ color: '#fff', fontSize: '1.2rem', margin: '1rem 0' }}>超级合伙人</div>
                        <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem' }}>
                            <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column' }}>
                                <img style={{ width: '6rem', height: '5rem', margin: '0 auto' }} src={Hehuoren}></img>
                                <img style={{ width: '4rem', height: '0.8rem', margin: '0 auto 1rem auto' }} src={HehuorenText}></img>
                            </div>
                            <div style={{ color: 'rgba(225,225,225,0.6)' }}>超级合伙人已领取总额</div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                                <div style={{ color: '#fff', fontSize: '1.2rem' }}>{allRewardPartner}</div>
                                <div style={{ color: 'rgba(255,255,255,0.6)' }}>FFT</div>
                            </div>
                        </div>
                        <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem', marginTop: '1rem' }}>
                            <div style={{ color: 'rgba(225,225,225,0.6)' }}>待领取数额</div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                                <div style={{ color: '#fff', fontSize: '1.2rem' }}>{myReward}</div>
                                <div style={{ color: 'rgba(255,255,255,0.6)' }}>FFT</div>
                            </div>
                            {/* <ButtonLight onClick={claimReward} style={{height:'1rem',margin:'1rem 0'}}>取出</ButtonLight> */}
                            <div style={{ color: 'rgba(225,225,225,0.6)' }}>我已获取的数额</div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                                <div style={{ color: '#fff', fontSize: '1.2rem' }}>{myAllRewardPartner}</div>
                                <div style={{ color: 'rgba(255,255,255,0.6)' }}>FFT</div>
                            </div>
                        </div>
                    </div>
                }

                <Snackbar
                    open={isShow}
                    autoHideDuration={3000}
                    message={msg}
                    onClose={handleClose}
                >
                </Snackbar>
            </Wrapper>
        </AppBody>
    )
}

export default Reward