import React, { useCallback, useEffect, useState } from 'react'
import AppBody from '../AppBody'
import { MaxButton, Wrapper } from '../Pool/styleds'
import FFT from '../../assets/rfs/FFT.png'
import XFFT from '../../assets/rfs/XFFT.png'
import PEC from '../../assets/rfs/PEC.png'
import XPEC from '../../assets/rfs/XPEC.png'
import Quick from '../../assets/rfs/quick.png'
import { Drawer } from '@material-ui/core'
import { useActiveWeb3React } from '../../hooks'
import { ButtonLight } from '../../components/Button'
import { useQuickContract, useTokenContract } from '../../hooks/useContract'
import { TransactionResponse } from '@ethersproject/providers';
import { useTransactionAdder } from '../../state/transactions/hooks'
import { calculateGasMargin } from '../../utils/index';
import { useContractAllowance } from '../../data/Allowances'
import { MaxUint256 } from '@ethersproject/constants';
import { BottomGrouping } from '../../components/swap/styleds'
import { toggleWalletModal } from '../../state/application/actions'
import BigNumber from "bignumber.js";
import styled from 'styled-components'
import Web3 from 'web3'
import TransactionConfirmationModal from '../../components/TransactionConfirmationModal'
import QuickABi from '../../constants/abis/quick.json'


const MaxBtn = styled.div`
background-color: #F7EB8D;
border-radius: 0.8rem;
color: #000;
cursor: pointer;
margin:0 1rem;
width: 3rem;
height: 1.8rem;
line-height: 1.8rem;
text-align: center;

`

const poolAddress = '0x5BC78cf20f2Ebb9Bd5Dd4629ECb49c271faf3432'


const QuickEX = () => {
    const { account } = useActiveWeb3React()
    const [show, setShow] = useState(false)
    const [current, setCurrent] = useState()
    const [amount, setAmount] = useState('')
    const [balance, setBalance] = useState('0')

    // modal and loading
    const [showConfirm, setShowConfirm] = useState(false)
    const [attemptingTxn, setAttemptingTxn] = useState(false) // clicked confirm
    const [txHash, setTxHash] = useState('')
    const [pendingText, setPendingText] = useState('')

    const list = [
        {
            eid: '0',
            name1: 'XFFT',
            name2: 'FFT',
            icon1: XFFT,
            icon2: FFT,
            address: '0x79B1b393DFe519e7B1c2A376C33FBDcD392f7a76'
        },
        {
            eid: '1',
            name1: 'XPEC',
            name2: 'PEC',
            icon1: XPEC,
            icon2: PEC,
            address: '0xD33278F4753011D42f201B76A974f3A2481aF134'
        }
    ]
    const addTransaction = useTransactionAdder()

    const tokenContract = useTokenContract(current ? current.address : '0x79B1b393DFe519e7B1c2A376C33FBDcD392f7a76')
    const approval = useContractAllowance(current ? current.address : '0x79B1b393DFe519e7B1c2A376C33FBDcD392f7a76', account, poolAddress)
    async function getBalance() {
        if (tokenContract) {
            const res = await tokenContract.balanceOf(account)
            console.log(res.toString(), 'balance')
            setBalance(res.toString())
        }
    }
    function max() {
        setAmount(Web3.utils.fromWei(balance, 'ether').toString())
    }
    async function handleApprove() {
        if (tokenContract) {
            const estimatedGas = await tokenContract.estimateGas.approve(poolAddress, MaxUint256).catch(() => {
                // general fallback for tokens who restrict approval amounts
                return tokenContract.estimateGas.approve(poolAddress, MaxUint256)
            })
            console.log(estimatedGas, 'estimatedGas')
            tokenContract
                .approve(poolAddress, MaxUint256, {
                    gasLimit: calculateGasMargin(estimatedGas)
                })
                .then((response) => {
                    addTransaction(response, {
                        summary: 'Approve ',
                        approval: { tokenAddress: current.address, spender: poolAddress, }
                    })
                })
                .catch((error) => {
                    console.debug('Failed to approve token', error)
                    throw error
                })
        }

    }
    async function handleQuick() {
        const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
        const contract = new web3.eth.Contract(QuickABi, poolAddress)
        let d = Web3.utils.toWei(amount.toString(), 'ether').toString()
        console.log('c')
        setShowConfirm(true)
        setAttemptingTxn(true)
        if (true) {
            setPendingText('闪兑')
            console.log(contract.methods,'methos')
            const gasLimit = await contract.methods.doExchange(current.eid, d).estimateGas({ from: account })
            console.log(gasLimit,'gasLimit')
            const tx = await contract.methods.doExchange(current.eid, d).send({ from: account, gas: gasLimit })
            console .log(tx,'tx')
            setAttemptingTxn(false)
            setTxHash(tx.transactionHash)
           
        }
    }
    const handleDismissConfirmation = useCallback(() => {
        setShowConfirm(false)
        // if there was a tx hash, we want to clear the input

        setTxHash('')
    }, [txHash])
    useEffect(() => {
        setCurrent(list[0])
        console.log(approval, 'approval')
    }, [])
    useEffect(() => {
        const t = setInterval(() => {
            getBalance()
        }, 4000)
        return (() => {
            clearInterval(t)

        })
    }, [account, tokenContract])

    return (
        <AppBody>
            <Wrapper>
                <TransactionConfirmationModal
                    isOpen={showConfirm}
                    attemptingTxn={attemptingTxn}
                    hash={txHash}
                    onDismiss={handleDismissConfirmation}
                    content={() => (
                        <div></div>
                    )}
                    pendingText={pendingText}
                />
                <div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '0.6rem' }}>
                        <div onClick={() => { setShow(true) }} style={{ display: 'flex', alignItems: 'center' }}>
                            <div style={{ fontSize: '1.2rem', marginLeft: '0.4rem' }}>∨</div>
                            <img style={{ width: '1.8rem', height: '1.8rem', margin: '0 0.4rem' }} src={current?.icon1}></img>
                            <div style={{ fontSize: '1.2rem' }}>{current?.name1}</div>
                        </div>
                        <img style={{ width: '1.4rem', height: '1.8rem' }} src={Quick}></img>
                        <div style={{ display: 'flex', alignItems: 'center' }}>
                            <img style={{ width: '1.8rem', height: '1.8rem' }} src={current?.icon2}></img>
                            <div style={{ fontSize: '1.2rem', margin: '0 0.4rem' }}>{current?.name2}</div>
                        </div>
                    </div>
                    <div>
                        <div style={{ display: 'flex', justifyContent: 'space-between', margin: '2rem 0', }}>
                            <div style={{ fontSize: '1rem' }}>兑换数量</div>
                            <div style={{ fontSize: '1rem' }}>{new BigNumber(Web3.utils.fromWei(balance, 'ether')).toFixed(7)}</div>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                            <input style={{ background: 'none', border: 'none', borderBottom: '1px #707070 solid', width: '70%', color: '#fff', fontSize: '1.2rem' }} value={amount} onChange={(v) => { setAmount(v.target.value) }}></input>
                            <MaxButton onClick={max}>Max</MaxButton>
                        </div>
                    </div>
                    <BottomGrouping >
                        {
                            !account ? (
                                <ButtonLight style={{ height: '1rem', margin: '1.6rem 0 1rem 0' }} onClick={toggleWalletModal}>解锁钱包</ButtonLight>
                            ) : !approval || approval == '0' ? (
                                <ButtonLight style={{ height: '1rem', margin: '1.6rem 0 1rem 0' }} onClick={handleApprove}>授权</ButtonLight>

                            ) :
                                <ButtonLight onClick={handleQuick} style={{ height: '1rem', margin: '1.6rem 0 1rem 0', height: '1rem' }}>兑换</ButtonLight>

                        }
                    </BottomGrouping>

                </div>
                <Drawer
                    anchor='bottom'
                    open={show}
                    onClose={() => { setShow(false) }}
                >
                    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column', margin: '2rem 0' }}>
                        {
                            list.map(p =>
                                <div key={p.eid} onClick={() => { setCurrent(p); console.log(p, 'p');setBalance('0'); setShow(false) }} style={{ display: 'flex', alignItems: 'center', marginBottom: '0.8rem' }}>
                                    <img style={{ width: '1.6rem', height: '1.6 rem' }} src={p.icon1}></img>
                                    <div style={{ fontSize: '1rem' }} >{p.name1}</div>
                                </div>
                            )
                        }
                    </div>
                </Drawer>
            </Wrapper>
        </AppBody>
    )


}

export default QuickEX