import React, { useEffect, useState } from 'react'
import AppBody from '../AppBody'
import { Wrapper } from '../Pool/styleds'
import LevelBg from '../../assets/rfs/level_bg.png'
import LevelUpIcon from '../../assets/rfs/levelup.png'
import CloseIcon from '../../assets/rfs/close.png'
import Axios from 'axios'
import { useActiveWeb3React } from '../../hooks'
import { baseUrl, baseUrlV1 } from '../../moonbase_address.json'
import styled from 'styled-components'
import { Box, LinearProgress, Drawer, Dialog, Snackbar } from '@material-ui/core'
import './index.css'
import { spawnSync } from 'child_process'
import { ButtonLight } from '../../components/Button'
import { useTokenContract } from '../../hooks/useContract'
import Web3 from 'web3'
import FFTABI from '../../constants/abis/token.json'
import { flexDirection } from 'styled-system'


const FFTAddress = '0xd8B712f0DE6Ea101F588712Ac5CB0d791Ca17FBA'


const BackImg = styled.div`
background: url(${LevelBg}) no-repeat;
background-size: cover;
width:100%;
height: 10rem;

`

const Level = () => {
    const { account } = useActiveWeb3React()
    const [isShow,setIsShow]=useState(false)
    const [levelData, setLevelData] = useState()
    const [percent, setPercent] = useState(100)
    const [canLevelUp, setCanLevelUp] = useState(false)
    const [levelList, setLevelList] = useState([])
    const [selectedLevel, setSelectedLevel] = useState()
    const [showSelectLevel, setShowSelectLevel] = useState(false)
    const [devAddress, setDevAddress] = useState()
    const FFTContract = useTokenContract(FFTAddress)
    const [showLoading, setShowLoading] = useState(false)
    const [showDialog, setShowDialog] = useState(false)
    const [rewards, setRewards] = useState('0')
    const [fftData, setFFTData] = useState()

    const getLevel = async () => {
        const url = `${baseUrlV1}/user-center/member-info`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'r')

        if (r.data.code == 200) {
            console.log('success');
            setLevelData(r.data.data)

            if (r.data.data.team_level_id == 0) {
                setCanLevelUp(true)
            } else {
                setCanLevelUp(false)

                let effective_day = r.data.data.effective_day
                let remain_day = r.data.data.remain_day
                let pass = effective_day - remain_day
                let pct = (pass / effective_day) * 100
                console.log(pct, 'pct');
                setPercent(pct)
            }
        } else {
            regAccount()
        }
    }
    const regAccount = async () => {
        const url = `${baseUrlV1}/index/get-account`
        let data = {
            address: account
        }
        const r = await Axios.post(url, data)
        console.log(r, 'r')
        if (r.data.code == 200) {
            getLevel()
        }
    }
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setIsShow(false);
      };
    const getLevelList = async () => {
        const url = `${baseUrlV1}/user-center/buy-condition`
        let header = {
            headers: {
                Authorization: account
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'r')
        setLevelList(r.data.data.level_list)
        setFFTData(r.data.data.pay_method[0])
    }
    const levelUp = async (level_id, coin_id, price, tx_hash) => {
        let url = `${baseUrlV1}/user-center/buy-member`
        let header = {
            headers: {
                Authorization: account
            }
        }
        let data = {
            level_id: level_id,
            coin_id: coin_id,
            coin_price: price,
            tx_hash: tx_hash
        }
        const res = await Axios.post(url, data, header)
        console.log(res, 'buy member')
    }
    const payFFT = async () => {


        const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
        const contract = new web3.eth.Contract(FFTABI, FFTAddress)
        console.log(selectedLevel.pledge_amount, 'p')
        let fftAmount = selectedLevel.pledge_amount / fftData.to_usd
        if (fftAmount == 0) {
            setIsShow(true)
            return
        }
        let d = Web3.utils.toWei(fftAmount.toString(), 'ether').toString()
        console.log(d, 'd')
        setShowLoading(true)
        try {
            const gasLimit = await contract.methods.transfer(devAddress, d).estimateGas({ from: account })
            const tx = await contract.methods.transfer(devAddress, d).send({ from: account, gas: gasLimit })
            console.log(tx, 'tx')
            await levelUp(selectedLevel.id, fftData.id, fftData.to_usd, tx.transactionHash)
            setShowLoading(false)
            setShowDialog(true)
        } catch (e) {
            console.log(e)
            setShowLoading(false)
        }

    }
    const initData = async () => {
        let url = `${baseUrl}/app/init-configs`
        const r = await Axios.get(url)
        console.log(r, 'config')
        setDevAddress(r.data.data.receiver_address)
    }
    const getFFTData = async () => {
        let url = `${baseUrlV1}/user-center/buy-condition`
        const r = await Axios.get(url)
        console.log(r, 'fftData')
        setFFTData(r.data.data[0])
    }

    useEffect(() => {
        getLevel()

    }, [account])
    useEffect(() => {
        if (canLevelUp) {
            getLevelList()
        }
    }, [canLevelUp])
    useEffect(() => {
        initData()
    }, [])
    useEffect(() => {

        const t = setInterval(() => {
            getLevel()
        }, 4000)

        return (() => {
            clearInterval(t)
        })
    }, [])

    return (
        <AppBody>
            <Wrapper>
                <div>
                    <BackImg>
                        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
                            <img style={{ width: '90px', height: '90px' }} src={baseUrl + levelData?.logo}></img>
                            <div style={{ color: '#EED388', fontSize: '0.9rem' }}>{levelData?.team_level_name}</div>
                            <Box sx={{ width: '90%', margin: '0.6rem 0 0.4rem 0' }}>
                                <LinearProgress color='primary' variant="determinate" value={percent}></LinearProgress>
                            </Box>
                            <div style={{ color: 'rgba(255,255,255,0.6)', fontSize: '0.7rem' }}>{levelData?.remain_day_str}</div>
                        </div>
                    </BackImg>
                    {
                        canLevelUp &&
                        <div style={{ backgroundColor: '#2C2D32', padding: '0.8rem', marginTop: '1.6rem' }}>

                            <div>
                                <div style={{ fontSize: '1rem', color: 'rgba(225,225,225,0.6)' }}>升级会员</div>
                                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                                    <div onClick={() => { setShowSelectLevel(true) }}>选择会员等级
                                        <span style={{ fontSize: '1.4rem' }}>▾</span>
                                    </div>
                                    {
                                        selectedLevel &&
                                        <div style={{ display: 'flex', alignItems: 'center' }}>
                                            <img style={{ width: '2rem', height: '2rem' }} src={baseUrl + selectedLevel.logo}></img>
                                            <div>{selectedLevel.level_name}</div>
                                        </div>
                                    }

                                </div>
                                {
                                    selectedLevel &&
                                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1rem' }}>
                                        <div style={{ color: 'rgba(255,255,255,0.6)' }}>
                                            需质押
                                            <span style={{ fontSize: '1.4rem', marginLeft: '0.4rem', color: '#E6BD85' }}>{(selectedLevel.pledge_amount / fftData.to_usd).toFixed(4)}</span>
                                        </div>
                                        <div>FFT</div>


                                    </div>
                                }
                                {
                                    selectedLevel &&
                                    <ButtonLight onClick={payFFT} style={{ height: '0.2rem', marginTop: '1rem' }}>确定</ButtonLight>

                                }
                            </div>
                            {/* :
                                <div>
                                    <div style={{ fontSize: '1rem', color: 'rgba(225,225,225,0.6)' }}>会员到期抵押自动返回</div>
                                    <div style={{fontSize: '1.4rem',color:'#fff',marginTop:'1.6rem',textAlign:'center'}}>
                                        {rewards}
                                        <span style={{color:'rgba(255,255,255,0.6)'}}>FFT</span>
                                    </div>

                                </div> */}




                        </div>
                    }

                </div>
            </Wrapper>
            <Drawer
                anchor='bottom'
                open={showSelectLevel}
                onClose={() => { setShowSelectLevel(false) }}
            >
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column', margin: '2rem 0' }}>
                    {
                        levelList.map(p =>
                            <div onClick={() => { setSelectedLevel(p); console.log(p, 'p'); setShowSelectLevel(false) }} key={p.id} style={{ display: 'flex', alignItems: 'center', marginBottom: '0.8rem' }}>
                                <img style={{ width: '1.6rem', height: '1.6 rem' }} src={baseUrl + p.logo}></img>
                                <div style={{ fontSize: '1rem' }} >{p.level_name}</div>
                            </div>
                        )
                    }
                </div>
            </Drawer>
            <Dialog open={showLoading} >
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column', margin: '3rem 0' }}>
                    <img style={{ width: '4rem', height: '4rem' }} src={LevelUpIcon}></img>
                    <div style={{ color: '#fff', marginTop: '1rem' }}>正在升级请稍后...</div>
                </div>
            </Dialog>
            <Dialog open={showDialog} >
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column', margin: '3rem 0' }}>
                    <img style={{ width: '4rem', height: '4rem' }} src={LevelUpIcon}></img>
                    <div style={{ color: '#fff', margin: '1rem 0' }}>升级成功</div>
                    <ButtonLight style={{ height: '1rem' }} onClick={() => { setShowDialog(false) }}>确定</ButtonLight>
                </div>
            </Dialog>
            <Snackbar
                open={isShow}
                autoHideDuration={3000}
                message='暂时无法购买'
                onClose={handleClose}
            >
            </Snackbar>

        </AppBody>
    )
}

export default Level