import React, { useEffect, useState } from 'react'

import AppBody from '../AppBody'
import { Wrapper } from '../Pool/styleds'
import Axios from 'axios'
import { baseUrlV1 } from '../../moonbase_address.json'
import { useActiveWeb3React } from '../../hooks'
import { changeDateTimeToStr } from '../../utils'
const Notice = () => {
    const { account } = useActiveWeb3React()
    const [list, setList] = useState([])
    const [showDetail, setShowDetail] = useState(false)
    const [noticeDetail, setNoticeDetail] = useState()

    const getNoticeList = async () => {
        const url = `${baseUrlV1}/basic/notice/list`
        let header = {
            headers: {
                "Show-Language": 'zh-CN'
            }
        }
        const r = await Axios.get(url, header)
        console.log(r, 'notic');
        let d = r.data.data.list.map(p => p.update_at = changeDateTimeToStr(p.update_at))
        setList(r.data.data.list)
    }
    const getNoticeDetail = async (id) => {
        console.log(id, 'id')
        const url = `${baseUrlV1}/basic/notice/detail?id=${id}`
        let header = {
            headers: {
                "Show-Language": 'zh-CN'
            }
        }

        const r = await Axios.get(url, header)
        console.log(r, 'detail');
        setShowDetail(true)
        setNoticeDetail(r.data.data)
    }
    useEffect(() => {
        getNoticeList()

    }, [])

    return (
        <AppBody>
            {
                showDetail ?
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderBottom: '1px #EFEFEF solid',marginBottom:'1rem',paddingBottom:'1rem' }}>
                        <div onClick={()=>{setShowDetail(false)}} style={{color: '#F0CD70', fontSize: '1.6rem',cursor:'pointer'}}>←</div>
                        <div style={{color: '#F0CD70', fontSize: '1.2rem'}}>详情</div>
                        <div style={{opacity:'0.001'}}>wd</div>
                    </div>
                    :
                    <div style={{textAlign:'center',color: '#F0CD70', fontSize: '1.2rem',marginBottom:'1rem'}}>公告</div>

            }

            <Wrapper>
                <div>
                    {
                        !showDetail ?
                            list.map(p =>
                                <div key={p.id} onClick={() => { getNoticeDetail(p.id) }} style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', backgroundColor: '#191919', padding: '0.4rem', borderBottom: '1px #EFEFEF solid' }}>
                                    <div >
                                        <div style={{ color: '#F0CD70', fontSize: '1rem' }}>{p.title}</div>
                                        <div style={{ color: '#898A8D', marginTop: '0.4rem' }}>{p.update_at}</div>
                                    </div>
                                    <div style={{ color: '#333333', fontSize: '2rem' }}>﹥</div>

                                </div>
                            )
                            :
                            <div>
                                <div style={{ color: '#F0CD70', fontSize: '1rem' }}>{noticeDetail?.title}</div>
                                <div style={{ color: '#898A8D', margin: '0.6rem 0' }}>{noticeDetail?.update_at}</div>
                                <div style={{ color: '#F0CD70', fontSize: '1rem' }}>{noticeDetail?.description}</div>
                            </div>
                    }

                </div>
            </Wrapper>
        </AppBody>

    )
}

export default Notice